import {
  observable, action, configure, runInAction, computed,
} from 'mobx';
import fetch from 'axios';
import { DEVICE_MOCK, DEVICE_AUTO_LOGS } from '../mock/device';

configure({ enforceActions: 'always' });

class DeviceStore {
  @observable devices = DEVICE_MOCK;

  @observable autoCheckLogs = DEVICE_AUTO_LOGS;

  @observable manualCheckLogs = DEVICE_AUTO_LOGS;

  formatCheckLogs() {
    const formatArray = [];
    this.autoCheckLogs.forEach((log) => {
      const tempLog = {
        deviceDesc: log.deviceDesc,
      };
      const itemExist = formatArray.find(item => item.deviceDesc === log.deviceDesc);
      const existItem = formatArray.find(item => item.deviceDesc === log.deviceDesc) || tempLog;
      const logTime = new Date(log.inputDt);
      const key = `${logTime.getHours()}:${logTime.getMinutes()}`;


      switch (log.doorStatus) {
        case 'active':
          existItem[key] = 2;
          break;
        case 'blocked':
          existItem[key] = 1;
          break;
        case 'disconnected':
          existItem[key] = 0;
          break;
        default:
          break;
      }

      if (!itemExist) {
        formatArray.push(existItem);
      }
    });
    return formatArray;
  }

  getCheckLogKeys() {
    const noDuplicateArray = [];
    this.autoCheckLogs.forEach((log) => {
      const logTime = new Date(log.inputDt);
      const logKey = `${logTime.getHours()}:${logTime.getMinutes()}`;
      if (noDuplicateArray.indexOf(logKey) < 0) {
        noDuplicateArray.push(logKey);
      }
    });
    return noDuplicateArray.sort((a, b) => parseFloat(a) - parseFloat(b));
  }

  formatDataForLineChart(key) {
    const formatArray = [];
    this.autoCheckLogs.forEach((log) => {
      const tempLog = {
        id: log.deviceDesc,
      };
      const itemExist = formatArray.find(item => item.id === log.deviceDesc);
      const existItem = formatArray.find(item => item.id === log.deviceDesc) || tempLog;
      if (!existItem.data) {
        existItem.data = [];
      }
      const logTime = new Date(log.inputDt);

      const data = {
        x: logTime.toISOString().slice(0, 16),
        y: log[key],
      };

      existItem.data.push(data);
      existItem.data = existItem.data.sort((a, b) => new Date(a.x) - new Date(b.x));


      if (!itemExist) {
        formatArray.push(existItem);
      }
    });
    return formatArray;
  }

  @computed get formattedManualCheckLogs() {
    return this.formatCheckLogs();
  }

  @computed get formattedAutoCheckLogs() {
    return this.formatCheckLogs();
  }

  @computed get autoCheckLogKeys() {
    return this.getCheckLogKeys();
  }

  @computed get manualCheckLogKeys() {
    return this.getCheckLogKeys();
  }

  @computed get distanceToTime() {
    return this.formatDataForLineChart('doorDistance');
  }

  @computed get signalToTime() {
    return this.formatDataForLineChart('nbSignalPwr');
  }

  @action handleAuthLogin = async () => {
    let res = {};
    try {
      res = await fetch({
        method: 'GET',
        url: '/wechat/login',
        headers: {
          'content-type': 'application/json',
        },
      });
    } catch (e) {
      res = e;
    }
    runInAction(() => {
      console.log(res);
      if (res.status !== 200 || !res.data.success) {
        console.error(res.message);
      } else {
        this.shouldRedirect = true;
        this.isLogin = true;
        // accountStore.updateAccount(res.data.data.user);
      }
    });
  }
}

export default new DeviceStore();
