import React, { Component } from 'react';
import { ResponsiveHeatMap } from '@nivo/heatmap';
import { observer } from 'mobx-react';
import DeviceStatus from '../../components/device-status';

import deviceStore from '../../stores/device-store';
import './index.css';

@observer
class HomePage extends Component {
  constructor(props) {
    super(props);
    this.heatmapProps = {
      indexBy: 'deviceDesc',
      forceSquare: true,
      cellShape: 'circle',
      minValue: 0,
      maxValue: 2,
      axisTop: {
        orient: 'top',
        tickSize: 0,
        tickPadding: 5,
        legend: '',
        legendOffset: 36,
      },
      padding: 6,
      axisLeft: {
        orient: 'left',
        tickSize: 0,
        tickPadding: 5,
        tickRotation: 0,
        legendPosition: 'middle',
        legendOffset: -40,
      },
      nanColor: '#c9c9c9',
      colors: ['#c9c9c9', '#C94E5D', '#5ed60e'],
      margin: {
        top: 30,
        bottom: 10,
      },
      animate: false,
      enableLabels: false,
      cellBorderColor: 'inherit:darker(0.4)',
      hoverTarget: 'rowColumn',
      cellHoverOthersOpacity: 0.25,
    };
  }

  render() {
    return (
      <div className="page page__home home">
        <div className="row">
          <DeviceStatus
            className="col col-12"
          />
        </div>
        <div className="row">
          <div className="col col-6 module module__heatmap heatmap">
            <div className="module__title">
              Auto Check Status
            </div>
            <div className="heatmap__chart">
              <ResponsiveHeatMap
                data={deviceStore.formattedAutoCheckLogs}
                keys={deviceStore.autoCheckLogKeys}
                {...this.heatmapProps}
              />
            </div>

          </div>
          <div className="col col-6 module module__heatmap heatmap">
            <div className="module__title">
              Manual Check Status
            </div>
            <div className="heatmap__chart">
              <ResponsiveHeatMap
                className="heatmap__chart"

                data={deviceStore.formattedManualCheckLogs}
                keys={deviceStore.manualCheckLogKeys}

                {...this.heatmapProps}
              />
            </div>
          </div>
        </div>

      </div>
    );
  }
}

export default HomePage;
