import React from 'react';
import { observer } from 'mobx-react';
import classnames from 'classnames';
import deviceStore from '../../stores/device-store';
import SvgIcon from '../basic/svg-icon';
import './index.css';

const DeviceStatus = ({ className }) => {
  const moduleClass = classnames(className, 'module module__device-status device-status');
  return (
    <div className={moduleClass}>
      <div className="module__title">Current Ultrasonic State</div>
      <div className="module__content">
        {
          deviceStore.devices.map((device) => {
            const deviceClass = classnames('device-status__device device', device.doorStatus);
            const batterySvgId = `battery-${device.batteryPwr * 100}`;

            return (
              <div key={device.deviceId} className={deviceClass}>
                <div className="device__title">
                  {`${device.deviceDesc}`}
                </div>
                <div className="device-status__light">
                  <SvgIcon
                    svgId="circle"
                    className="state-light__svg"
                  />
                </div>
                <div className="device-status__battery">
                  <SvgIcon
                    className="battery__svg"
                    svgId={batterySvgId}
                  />
                </div>
              </div>
            );
          })
        }
      </div>

    </div>
  );
};

export default observer(DeviceStatus);
